.App {
  width: 100%;
  height: 100vh;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.Sidebar{
  height: 100%;
  width: 250px;
  background-color: #2d445d;
}

.SidebarList{
  width: 100%;
  height: auto;
  padding: 0;
}

.SidebarList .row{
  width: 100%;
  height: 60px;
  display: flex;
  color: #fff;
  justify-content: center;
  align-items: center;
}

.SidebarList .row:hover{
  cursor: pointer;
  background-color: #3b627a;
}

.row #icon{
  flex: 30%;
  display: grid;
  place-items: center;
}

.row #title{
  flex: 70%;
}

.SidebarIcon{
  padding-top: 20px;
  padding-bottom: 15px;
  text-align: center;
}

.SidebarIcon{
  color: #fff;
  font-weight: 600;
  font-size: 13px;

}